import React from 'react';
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import { SearchBar} from 'antd-mobile'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Order from './taobao/Order';
import My from './taobao/my';
import Cup from './home/cup';
import Cup2 from './home/cup2';
import School from './home/school';
import Mp3 from './home/mp3';
import Box from './home/box';
import Box2 from './home/box2';
import Plant from './home/plant';
import Shoes from './home/shoes';
import Water from './home/water';
import Kouhong from './home/kouhong';
import store from './reduxDemo/store';
import Search from './home/Search'
import { Provider } from 'react-redux'

const App = ()=>{
	return <Provider store={store}>
	<Router>
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/cart' component={Cart} />
			<Route path='/order' component={Order} />
			<Route path='/my' component={My} />
			<Route path='/home/cup' component={Cup} />
			<Route path='/home/cup2' component={Cup2} />
			<Route path='/home/box' component={Box} />
			<Route path='/home/box2' component={Box2} />
			<Route path='/home/mp3' component={Mp3} />
			<Route path='/home/plant' component={Plant} />
			<Route path='/home/shoes' component={Shoes} />
			<Route path='/home/school' component={School} />
			<Route path='/home/water' component={Water} />
			<Route path='/home/kouhong' component={Kouhong} />
			<Route path='/home/Search' component={Search} />
			{/* <Route component={Home}/> */}
			<Route render={()=>
				<Redirect to='/'></Redirect>}
			/>
		</Switch>
		<div>
            <Mytab />
            </div>	
	</Router>
</Provider>
}
export default App;







