import React from 'react'
import Bottom from '../taobao/Bottom'

const Plant = (props) => {
    return (
        <div>
            <img 
                style={{width:'100%'}} 
                src='https://gw.alicdn.com/bao/uploaded/i3/2268780925/O1CN01Qitz7o1IhgvhyGvWK_!!2268780925.jpg_500x500q90.jpg_.webp'
                alt=""
            />
            <p>陶瓷</p>
            <p>19</p>  
            <div>
                <button
                    style={{height:'50px',width:'100%'}}
                    onClick={()=>props.history.push('/taobao/Home')}
                >
                退出
                </button>
            </div>
            <div>
                <Bottom></Bottom>
            </div>
        </div>
    )
}

export default Plant