import React from 'react'
import Bottom from '../taobao/Bottom'


const Shoes = (props) => {
    return (
        <div>
            <img 
                style={{width:'100%'}} 
                src='https://gw.alicdn.com/bao/uploaded/i3/1107279238/O1CN01TYsSnE2I73TioPsnx_!!0-item_pic.jpg_500x500q90.jpg_.webp'
                alt=""
            />
            <p>床上四件套</p>
            <p>19</p>  
            <div>
                <button
                    style={{height:'50px',width:'100%'}}
                    onClick={()=>props.history.push('/taobao/Home')}
                >
                退出
                </button>
            </div>
            <div>
                <Bottom></Bottom>
            </div>
        </div>
    )
}

export default Shoes