import React from 'react'
import {connect} from 'react-redux'

const Cart = (props) => {
    return (
        <div>
            <div>
            收藏列表
            <button onClick={props.history.goBack}>返回</button>
            {
            props.mydata.map((item,idx)=>(
                <li key={idx}>
                    {item}
                    <button
                        onClick={()=>{
                            props.dispatch({
                                type: "DEL_TODO",
                                index: idx
                            })
                        }}
                    >删除</button>
                </li>
            ))
        }
        </div>
            
            
        </div>   
    )
}
const mapStateToProps = (state)=>{
    return {
        mydata:state.todo
    }
}

export default connect(mapStateToProps)(Cart)

