import React from 'react';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux';

const Buttom = (props) => {
        return (
            <div style={{
              height:'100px'
              }}>
                <div className='buttom' style={{height:'50px',display:"flex",width:'100%'}}>
                    <div style={{height:'50px',display:"flex",width:'100%'}}>
                      <button style={{width:'100%',backgroundColor:'white'}}>店铺</button>
                    </div>
                    <div style={{height:'50px',display:"flex",width:'100%'}}>
                      <button style={{width:'100%',backgroundColor:'white'}}>客服</button>
                    </div>
                    <div style={{height:'50px',display:"flex",width:'100%'}}>
                    <button style={{width:'100%',backgroundColor:'white'}}
                            onClick={(e)=>{
                              if(props.userinfo==''){props.history.push('./denglu');}
                              props.dispatch({
                                type: "ADD_TODO",
                                value:e.target.value
                              })
                            }}
                    ><span>收藏</span></button>
                    </div>
                    <div style={{height:'50px',display:"flex",width:'100%'}}>
                      <button style={{width:'100%',backgroundColor:'#FAAF76'}}>加入购物车</button>
                    </div>
                    <div style={{height:'50px',display:"flex",width:'100%'}}>
                      <button style={{width:'100%',backgroundColor:'#FF0000'}}>立即购买</button>
                    </div>
                </div>
                
            </div>
        );
}
export default connect(withRouter)(Buttom);

