import React from 'react'
import Bottom from '../taobao/Bottom'


const Mp3 = (props) => {
    return (
        <div>
            <img 
                style={{width:'100%'}} 
                src='https://gw.alicdn.com/bao/uploaded/i1/2457662309/O1CN01PJ1b791SvZ5KTPZa7_!!0-item_pic.jpg_500x500q90.jpg_.webp'
                alt=""
            />
            <p>大袋子</p>
            <p>19</p>  
            <div>
                <button
                    style={{height:'50px',width:'100%'}}
                    onClick={()=>props.history.push('/taobao/Home')}
                >
                退出
                </button>
            </div>
            <div>
                <Bottom></Bottom>
            </div>
        </div>
    )
}

export default Mp3