let data={
    datalist:[{},{}],

    shoucang:[,,,],

    userinfo:'',
}

const homereducer = (state=data,action)=>{
    switch(action.type){
        case 'LOGIN':
            // Object.assign({},state,{username:'123'})
            return {username:action.username}
        case'GETLIST':
            return Object.assign({},state,{datalist:action.datalist})
        default:
            return state;
        
    }
}

export default homereducer;